body {
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Geometria-Light", Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.2;
  color: #000;
}

.container {
  position: relative;
  z-index: 1;
  max-width: 930px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  margin: 0 -15px;
}

.col {
  padding: 0 15px;
}

.main {
  display: block;
  overflow: hidden;
}

.title {
  font-family: "Geometria", Arial, sans-serif;
  font-weight: normal;
  font-size: 24px;
  line-height: 1.2;
  color: #000;
}

.btn {
  position: relative;
  display: inline-block;
  outline: 0;
  text-align: center;
  border-radius: 28px;
  cursor: pointer;
  text-transform: uppercase;
  transition: .3s ease-in-out;
}

.btn_transparent {
  min-width: 110px;
  padding: 5px 5px 3px;
  font-family: "Geometria-Medium", Arial, sans-serif;
  font-weight: normal;
  font-size: 11px;
  line-height: 20px;
  border: 1px solid #3eb4f0;
  color: #3eb4f0;
}

.btn_transparent:hover {
  color: #fff;
  background-color: #3eb4f0;
}

.btn_blue {
  padding: 17px 56px 16px;
  background-color: #3eb4f0;
  color: #fff;
  text-transform: none;
  font-family: "Geometria", Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
}

.btn_blue:hover {
  background-color: #fff;
  color: #3eb4f0;
}

.btn_white {
  min-width: 110px;
  padding: 6px 5px 4px;
  font-family: "Geometria-Medium", Arial, sans-serif;
  font-weight: normal;
  font-size: 11px;
  line-height: 20px;
  color: #181818;
  background-color: #fff;
  transition: .5s;
}

.btn_white:hover:after, .btn_white:hover:before {
  transform: scale(1);
}

.btn_white:after, .btn_white:before {
  width: 100%;
  height: 100%;
  z-index: 3;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(0);
  transition: .5s;
  border-radius: 18px;
}

.btn_white:before {
  border-bottom: 1px solid #3eb4f0;
  border-left: 1px solid #3eb4f0;
  transform-origin: 0 100%;
}

.btn_white:after {
  border-top: 1px solid #3eb4f0;
  border-right: 1px solid #3eb4f0;
  transform-origin: 100% 0;
}

.ask-block__title {
  margin-bottom: 25px;
}

.ask-block__inner {
  max-width: 805px;
  margin-left: 30px;
}

@media only screen and (max-width: 992px) {
