    margin-left: 0;
  }
}

.ask-block__item {
  margin-bottom: 27px;
}

.ask-block__item-title {
  margin-bottom: 12px;
}

.ask-block__question {
  display: block;
}

.ask-block__question:not(:last-child) {
  margin-bottom: 10px;
}

.ask-block__input {
  display: none;
}

.ask-block__input:checked ~ .ask-block__text {
  background-color: #3eb4f0;
  color: #fff;
}

.ask-block__text {
  padding: 3px 13px 2px;
  border: 1px solid #3eb4f0;
  cursor: pointer;
  color: #010101;
  border-radius: 10px;
  transition: .3s ease-in-out;
}

.ask-block__actions {
  text-align: center;
}

.scroll-btn {
  width: 30px;
  height: 60px;
  display: flex;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
}

.scroll-btn__arr {
  width: 30px;
  height: 30px;
  position: relative;
  box-shadow: 6px 6px #3eb4f0;
  transform: rotate(45deg);
}

.scroll-btn__arr:nth-child(1) {
  animation: arrs-anim-2 1s steps(5, start) infinite;
}

.scroll-btn__arr:nth-child(2) {
  margin-top: -15px;
  animation: arrs-anim-1 1s steps(5, start) infinite;
}

@keyframes arrs-anim-1 {
  0% {
    opacity: 0.1;
  }
  11% {
    opacity: 0.15;
  }
  22% {
    opacity: 0.25;
  }
  33% {
    opacity: 0.5;
  }
  44% {
    opacity: 1;
  }
  55% {
    opacity: 0.5;
  }
  66% {
    opacity: 0.25;
  }
  77% {
    opacity: 0.15;
  }
  88% {
    opacity: 0.1;
  }
  100% {
    opacity: 0.1;
  }
}

@keyframes arrs-anim-2 {
  0% {
    opacity: 0.25;
  }
  11% {
    opacity: 0.5;
  }
  22% {
    opacity: 1;
  }
  33% {
    opacity: 0.5;
  }
  44% {
    opacity: 0.25;
  }
  55% {
    opacity: 0.15;
  }
  66% {
    opacity: 0.1;
  }
  77% {
    opacity: 0.1;
  }
  88% {
    opacity: 0.1;
  }
  100% {
    opacity: 0.15;
  }
}

