body {
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   @include f('geo300', 18);
   line-height: 1.2;
   color: #000; }

.container {
   position: relative;
   z-index: 1;
   max-width: 930px;
   margin: 0 auto;
   padding: 0 15px; }

.row {
   display: flex;
   margin: 0 -15px; }

.col {
   padding: 0 15px; }

.main {
   display: block;
   overflow: hidden; }

.title {
   @include f('geo400', 24);
   line-height: 1.2;
   color: #000; }

.btn {
   position: relative;
   display: inline-block;
   outline: 0;
   text-align: center;
   border-radius: 28px;
   cursor: pointer;
   text-transform: uppercase;
   transition: .3s ease-in-out;
   &_transparent {
      min-width: 110px;
      padding: 5px 5px 3px;
      @include f('geo500', 11, 20);
      border: 1px solid $color1;
      color: $color1;
      &:hover {
         color: #fff;
         background-color: $color1; } }
   &_blue {
      padding: 17px 56px 16px;
      background-color: $color1;
      color: #fff;
      text-transform: none;
      @include f('geo400', 18);
      &:hover {
         background-color: $white;
         color: $color1; } }
   &_white {
      min-width: 110px;
      padding: 6px 5px 4px;
      @include f('geo500', 11, 20);
      color: #181818;
      background-color: $white;
      transition: .5s;
      &:hover {
         &:after, &:before {
            transform: scale(1); } }
      &:after, &:before {
         width: 100%;
         height: 100%;
         z-index: 3;
         content: '';
         position: absolute;
         top: 0;
         left: 0;
         transform: scale(0);
         transition: .5s;
         border-radius: 18px; }
      &:before {
         border-bottom: 1px solid $color1;
         border-left: 1px solid $color1;
         transform-origin: 0 100%; }
      &:after {
         border-top: 1px solid $color1;
         border-right: 1px solid $color1;
         transform-origin: 100% 0; } } }
